* 
{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

div,
h1,
h3,
h5,
span,
strong,
img 
{
    display: block;
}
ul
{
    list-style: none;
}
h5
{
    text-align: center;
}
h1,
h3,
h5
{
    text-align: center;
    font-family: 'Cera Round Pro Bold';
}

strong
{
    font-family: 'Cera Round Pro Bold';
}
html 
{
    font-size: 16px;
}

body 
{
    height: 100vh;
    font-family: Cera Round Pro Regular;
    overflow-x: hidden;
    color: rgb(24, 49, 83);
}